"use client";

import { useAnalytics } from "@/hooks/useAnalytics";
import { BarChart2, CheckSquare, Book, Flame } from "lucide-react";

export default function AnalyticsPage() {
  const { stats, loading } = useAnalytics();

  if (loading) {
    return <div className="p-8 text-[var(--color-text)]">Loading analytics...</div>;
  }

  return (
    <div className="mx-auto max-w-4xl">
      <h1 className="mb-8 text-4xl font-bold" style={{ color: "var(--color-text)" }}>Analytics Dashboard</h1>

      <div className="grid gap-6 md:grid-cols-3">
        <div className="cozy-card p-6">
          <div className="mb-4 flex items-center justify-between">
            <h3 className="font-bold" style={{ color: "var(--color-text)" }}>Total Tasks</h3>
            <CheckSquare className="h-5 w-5 text-[var(--color-primary)]" />
          </div>
          <p className="text-3xl font-bold" style={{ color: "var(--color-text)" }}>{stats.completedTodos + stats.activeTodos}</p>
          <p className="text-sm opacity-70" style={{ color: "var(--color-text)" }}>{stats.completedTodos} completed</p>
        </div>

        <div className="cozy-card p-6">
          <div className="mb-4 flex items-center justify-between">
            <h3 className="font-bold" style={{ color: "var(--color-text)" }}>Journal Entries</h3>
            <Book className="h-5 w-5 text-[var(--color-secondary)]" />
          </div>
          <p className="text-3xl font-bold" style={{ color: "var(--color-text)" }}>{stats.totalJournalEntries}</p>
          <p className="text-sm opacity-70" style={{ color: "var(--color-text)" }}>Lifetime entries</p>
        </div>

        <div className="cozy-card p-6">
          <div className="mb-4 flex items-center justify-between">
            <h3 className="font-bold" style={{ color: "var(--color-text)" }}>Habit Streaks</h3>
            <Flame className="h-5 w-5 text-[var(--color-danger)]" />
          </div>
          <p className="text-3xl font-bold" style={{ color: "var(--color-text)" }}>{stats.habitStreaks}</p>
          <p className="text-sm opacity-70" style={{ color: "var(--color-text)" }}>Total streak days</p>
        </div>
      </div>

      <div className="cozy-card mt-8 flex h-64 items-center justify-center p-8">
        <div className="text-center opacity-50">
          <BarChart2 className="mx-auto mb-2 h-12 w-12 text-[var(--color-text)]" />
          <p style={{ color: "var(--color-text)" }}>Detailed charts coming soon...</p>
        </div>
      </div>
    </div>
  );
}
