"use client";

import { useState, useEffect } from "react";
import { collection, query, where, getDocs } from "firebase/firestore";
import { db } from "@/lib/firebase";
import { useAuth } from "@/contexts/AuthContext";

export function useAnalytics() {
    const [stats, setStats] = useState({
        completedTodos: 0,
        activeTodos: 0,
        totalJournalEntries: 0,
        activeHabits: 0,
        habitStreaks: 0,
    });
    const [loading, setLoading] = useState(true);
    const { user } = useAuth();

    useEffect(() => {
        async function fetchStats() {
            if (!user) return;

            try {
                // Todos
                const todosQuery = query(
                    collection(db, "todos"),
                    where("userId", "==", user.uid)
                );
                const todosSnapshot = await getDocs(todosQuery);
                const todos = todosSnapshot.docs.map((doc) => doc.data());
                const completedTodos = todos.filter((t) => t.completed).length;
                const activeTodos = todos.length - completedTodos;

                // Journal
                const journalQuery = query(
                    collection(db, "journal"),
                    where("userId", "==", user.uid)
                );
                const journalSnapshot = await getDocs(journalQuery);
                const totalJournalEntries = journalSnapshot.size;

                // Habits
                const habitsQuery = query(
                    collection(db, "habits"),
                    where("userId", "==", user.uid)
                );
                const habitsSnapshot = await getDocs(habitsQuery);
                const habits = habitsSnapshot.docs.map((doc) => doc.data());
                const activeHabits = habits.length;
                const habitStreaks = habits.reduce((acc, curr) => acc + curr.streak, 0);

                setStats({
                    completedTodos,
                    activeTodos,
                    totalJournalEntries,
                    activeHabits,
                    habitStreaks,
                });
            } catch (error) {
                console.error("Error fetching analytics:", error);
            } finally {
                setLoading(false);
            }
        }

        fetchStats();
    }, [user]);

    return { stats, loading };
}
